*::before,
*::after {
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
  font-size: 10px;
}

ul,
ol,
li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #422779;
}

html {
  font-size: 10px;
  font-family: "Quicksand", sans-serif;
}
@media (max-width: 668px) {
  html {
    font-size: 6px;
  }
}

body {
  background-color: #ff8e08;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.title {
  font-family: "Open Sans", sans-serif;
  font-size: 6.4rem;
  font-style: italic;
  font-weight: 800;
  line-height: 105%;
  color: #218888;
}

.basic-text {
  font-family: "Quicksand", sans-serif;
  font-size: 6.4rem;
  font-style: normal;
  font-weight: 600;
  line-height: 105%;
  color: #650e70;
}

.button-basic {
  display: block;
  width: 15rem;
  height: 8rem;
  background-color: #f5f5f5;
  border-radius: 1rem;
  border: solid 2px #3a4e58;
  text-align: center;
  font-family: "Quicksand", sans-serif;
  font-size: 2.4rem;
  font-style: normal;
  font-weight: 800;
  line-height: 105%;
  color: #ff5a00;
  cursor: pointer;
}
.button-basic:hover {
  background-color: #650e70;
  color: #f5f5f5;
}
.button-basic.active {
  background-color: #ff5a00;
  color: #650e70;
}

.input_login {
  width: 19.4rem;
  height: 2.3rem;
  border: #3a4e58 2px solid;
  border-radius: 0.6rem;
}
.input_login::placeholder {
  font-family: "Quicksand", sans-serif;
  font-size: 0.9rem;
  font-style: italic;
  font-weight: 600;
  line-height: 105%;
  color: #3a4e58;
  padding-left: 1rem;
